$bigBorderRadius: 2rem;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #00c6ff;
    width: 100%;
    height: 100vh;
}

#root {
    all: inherit;
}

.App {
    ::-webkit-scrollbar {
        width: 0.5rem;
        height: 0.5rem;
    }
    ::-webkit-scrollbar-thumb {
        border: 1px solid;
        background: linear-gradient(transparent, #0094bd);
        border-radius: 6px;
    }
    ::-webkit-scrollbar-thumb:hover {
        background: linear-gradient(#20ff0022, #20ff00);
    }
    height: 100%;
    width: 100%;
    min-width: 900px;
    background: linear-gradient(to left bottom, #65dfc9, #6cdbeb);
    font-family: "Poppins", sans-serif;

    .btn-type-1 {
        max-width: 200px;
        max-height: 30px;
        display: inline-block;
        outline: none;
        border: 1px solid #000000;
        background-color: #bdf1f6;
        color: black;
        padding: 0.2rem;
        border-radius: 0.105rem;
        cursor: pointer;
        transition: 0.05s ease;
        box-shadow: 0px 0px 5px rgba(122, 122, 122, 0.8);
    }

    .btn-type-1:hover {
        background: transparent;
    }

    label {
        font-size: 0.7rem;
    }

    .main-container {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 80%;
        width: 80%;
        background: linear-gradient(to left top, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.5));
        border-radius: $bigBorderRadius;
        z-index: 2;
        backdrop-filter: blur(5rem);
        position: relative;
        display: flex;

        &::before,
        &::after {
            content: "";
            background: white;
            background: linear-gradient(to left top, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.3));
            height: 10rem;
            width: 10rem;
            position: absolute;
            border-radius: 50%;
            z-index: 1;
        }
        &::before {
            top: -2%;
            left: -10%;
        }
        &::after {
            bottom: -2%;
            right: -10%;
        }

        .todos-container {
            width: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 1rem;
            margin: 1rem;

            .todos-header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 1rem;
                height: 15%;
            }

            .current-todos {
                height: 70%;
                padding: 0.4rem;
                margin: 0.4rem;
                display: flex;
                flex-wrap: wrap;
                overflow-y: scroll;
                border-bottom: 1px solid #000;

                .todo-content {
                    padding: 0.5rem;

                    div:first-of-type {
                        min-height: 30%;

                        input:first-of-type {
                            height: 100%;
                            font-size: 1rem;
                            font-weight: 1000;
                            display: inline-block;
                        }
                    }

                    input:first-of-type {
                        height: 100%;
                        font-size: 1rem;
                        font-weight: 1000;
                        display: inline-block;
                    }

                    textarea {
                        font-size: 0.8rem;
                        font-weight: 100;
                        width: 100%;
                        display: inline-block;
                    }

                    input:last-of-type {
                        height: 100%;
                        font-size: 0.8rem;
                        font-weight: 100;
                        display: inline-block;
                    }
                }

                .todo-content textarea {
                    border-bottom: 1px solid #000;
                }
            }

            .quick-controls-container {
                display: flex;
                justify-content: space-around;
                align-items: center;
                height: 15%;
            }
        }

        .side-container {
            width: 30%;
            display: flex;
            flex-direction: column;
            align-items: center;
            background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
            border-radius: 0 2rem 2rem 0;
            padding: 0.5rem;

            .controls-container {
                height: 50%;
                width: 90%;
                border-bottom: 1px solid #000;
                margin: 0.2rem;
            }

            .cleared-todos-container {
                height: 50%;
                width: 90%;

                .cleared-todos-header {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    height: 15%;
                }

                .cleared-todos-wrapper {
                    display: flex;
                    flex-wrap: wrap;
                    height: 85%;
                    overflow-y: scroll;
                }
            }
        }
    }
}

@import "./styles/AddTodoForm";
@import "./styles/Todo";
@import "./styles/TodoList";

@media all and (min-width: 1024px) {
    html {
        font-size: 20px;
    }
}

@media all and (max-width: 1024px) {
    html {
        font-size: 12px;
    }
}

@media all and (max-width: 768px) {
    html {
        font-size: 10px;
    }
}
